<template>
  <div class="app-container">
    <!-- Left menu -->
    <div class="left-menu">
      <h2>QR.CSS</h2>
      <ul>
        <li @click="loadComponent('CssHome')">QR.CSS Home</li>
        <li @click="loadComponent('CssButtons')">QR.CSS button</li>
        <li @click="loadComponent('CssCards')">QR.CSS Cards</li>
        <li @click="loadComponent('Input')">QR.CSS Input</li>
        <li @click="loadComponent('CssButtonsDown')">QR.CSS Download</li>
        <li @click="loadComponent('CssButtonsDown2')">QR.CSS Download2</li>
        <li @click="loadComponent('CssButtonsDown3')">QR.CSS Download3</li>
        <li @click="loadComponent('CssButtonsDown4')">이미지생성</li>
        <li @click="loadComponent('CssHome')">QR.CSS Effect</li>
        <li @click="loadComponent('effect01')">QR.CSS effect01</li>
        <li @click="loadComponent('effect02')">QR.CSS effect02</li>
        <li @click="loadComponent('Convert01')">SVG Convert</li>
        <li @click="loadComponent('Convert02')">html2canvas</li>
      </ul>
    </div>

    <!-- Right content area -->
    <div class="right-content">
      <component :is="currentComponent"></component>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      currentComponent: null
    }
  },
  methods: {
    loadComponent (componentName) {
      // Dynamically load the selected Vue component
      import(`../qrcss/${componentName}.vue`)
        .then(component => {
          this.currentComponent = component.default || component
        })
        .catch(error => {
          console.error(`Failed to load component: ${componentName}`, error)
        })
    }
  }
}
</script>

<style>
body {
 background-color: rgb(240, 244, 251);
  background-color:#FFFFFF;
}
/* root css*/
.center-column {
    width: 90%; /* Fixed width */
  }
.left-column, .right-column {
  display: block;
  width: calc((100% - 90%) / 2);
}

@media screen and (min-width: 1024px) {
  .main {
    justify-content: flex-start; /* Align children to the start of the main axis */

  }

  .center-column {
    width: 100%; /* Fixed width */
  }

  .left-column, .right-column {
    display: block;
    width: calc((100% - 1000px) / 2); /* Distribute remaining space equally */
  }
}

/* qr css */
.app-container {
  display: flex;
}

